import {lazy, Suspense} from 'react';
// import { BrowserRouter as Router, Route, Switch, Routes } from 'react-router-dom';
import { HashRouter as Router, Route, Switch } from 'react-router-dom';

// import {MainPage, ComicsPage, Page404, SinglePage, SingleComicPage, SingleCharacterLayout} from '../pages';

import AppHeader from "../appHeader/AppHeader";
import Spinner from '../spinner/Spinner';

const Page404 = lazy(() => import('../pages/404'));
const MainPage = lazy(() => import('../pages/MainPage'));
const ComicsPage = lazy(() => import('../pages/ComicsPage'));
const SingleComicPage = lazy(() => import('../pages/singleComicPage/SingleComicPage'));
const SingleCharPage = lazy(() => import('../pages/singleCharPage/SingleCharPage'))
const SinglePage = lazy(() => import('../pages/SinglePage'))

const App = () => {
    
    return (
        <Router>
            <div className="app">
                <AppHeader/>
                <main>
                    <Suspense fallback={<Spinner/>}>
                        <Routes>
                            <Route exact path="/marvel" element={<MainPage/>} />
                            <Route exact path="/comics" element={<ComicsPage />} />
                            <Route exact path="/comics/:id" element={<SinglePage Component={SingleComicPage} dataType='comic'/>} />
                            <Route exact path="/characters/:id" element={<SinglePage Component={SingleCharPage} dataType='character'/>} />
                            <Route path='*' element={<Page404 />}>
                            </Route>
                        </Routes>
                    </Suspense>
                </main>
            </div>
        </Router>
    )
}

export default App;


